html, body, .App {
  height: 100vh;
  overflow: hidden;
}

.App-body {
  /* text-align: center; */
  height: 100%;
  overflow: scroll;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  /* color: #60dbfb; */
  background-color: transparent;
  font-size: 64px;
  height: 70px;
  position: fixed;
  padding-left: 25px;
  z-index: 2;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/***********/

.App > .row > div {
  padding: 0;
  height: 100vh;
}
